/* ==========================================================================
   Sticky header style - begin
   ========================================================================== */

$sticky-header-height: 60px;

.mkdf-sticky-header {
	position: fixed;
	top: 0;
	left: 0;
	height: $sticky-header-height;
	width: 100%;
    z-index: 100;
	-webkit-backface-visibility: hidden;
	@include mkdfTransform(translateY(-100%));
	@include mkdfTransitionTransform(.6s ease);
    
    .mkdf-grid {
        height: 100% !important; //override settings for other instances of this div
    }
    
    &.header-appear {
        transform: translateY(0%);
    }
    
    .mkdf-sticky-holder {
	    position: relative;
	    height: 100%;
        z-index: 2;
        background-color: #000;
        box-sizing: border-box;
	
	    .mkdf-logo-wrapper a {
		    max-height: $sticky-header-height;
	    }
    }
    
    .admin-bar &.header-appear {
        top: 32px;
    }
	
	.mkdf-position-center-inner,
	.mkdf-position-right-inner {
		height: 100%;
	}
	
	.mkdf-sticky-right {
		display: inline-block;
		vertical-align: middle;
	}
	
	.widget {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}

    .mkdf-main-menu {
        > ul > li  {
            &:last-child {
                > a {
                    padding-right: 0;
                }
            }
        }
    }
}
/* ==========================================================================
   Sticky header style - end
   ========================================================================== */