/* ==========================================================================
   Top header style - begin
   ========================================================================== */

.mkdf-top-bar {
	@include mkdfRelativeHolderLayout();
	height: 46px;
	color: #fff;
	font-size: 11px;
	line-height: 1;
	background-color: $default-heading-color;
	z-index: 111;
	
	> .mkdf-vertical-align-containers {
		padding: 0 60px;
	}
	
	.mkdf-grid {
		height: 100%;
		
		.mkdf-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;
			
			.mkdf-top-bar-in-grid-padding & {
				padding: 0 60px;
			}
		}
	}
	
	.widget {
		margin: 0;

		&.widget_archive,
		&.widget_calendar,
		&.widget_categories,
		&.widget_pages,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_rss,
		&.widget_search,
		&.widget_text,
		&.widget_nav_menu {
			display: none;
		}
	}
}
/* ==========================================================================
   Top header style - end
   ========================================================================== */